import React from 'react';
import styled from '@emotion/styled';
import { theme } from '../../styles/theme';

const Section = styled.section`
  padding: ${theme.spacing['2xl']} ${theme.spacing.xl};
  padding-top: calc(${theme.spacing['2xl']} + 80px);
  background: white;
  min-height: 100vh;
`;

const Container = styled.div`
  max-width: 800px;
  margin: 0 auto;
`;

const Title = styled.h1`
  font-family: ${theme.typography.fontFamily.heading};
  font-size: ${theme.typography.fontSize['4xl']};
  color: ${theme.colors.text.primary};
  margin-bottom: ${theme.spacing.xl};
`;

const Content = styled.div`
  color: ${theme.colors.text.secondary};
  font-size: ${theme.typography.fontSize.lg};
  line-height: 1.8;

  h2 {
    font-family: ${theme.typography.fontFamily.heading};
    font-size: ${theme.typography.fontSize['2xl']};
    color: ${theme.colors.text.primary};
    margin: ${theme.spacing.xl} 0 ${theme.spacing.md};
  }

  p {
    margin-bottom: ${theme.spacing.lg};
  }

  ul {
    margin-bottom: ${theme.spacing.lg};
    padding-left: ${theme.spacing.xl};
  }

  li {
    margin-bottom: ${theme.spacing.md};
  }
`;

export const PrivacyPolicy: React.FC = () => {
  return (
    <Section>
      <Container>
        <Title>Poltica de Privacidade</Title>
        <Content>
          <p>
            A sua privacidade  importante para ns.  poltica do Davida respeitar a sua privacidade
            em relao a qualquer informao sua que possamos coletar em nosso app e outros sites que possumos e operamos.
          </p>

          <h2>Informaes que coletamos</h2>
          <p>
            Coletamos informaes quando voc se registra em nosso app, realiza um cadastro e interage
            com outros recursos do aplicativo. As informaes coletadas incluem:
          </p>
          <ul>
            <li>Nome e informaes de contato</li>
            <li>Informaes demogrficas</li>
            <li>Dados sobre sua gravidez e sade</li>
            <li>Interaes com outros usurios</li>
          </ul>

          <h2>Como usamos suas informaes</h2>
          <p>
            Utilizamos as informaes que coletamos para:
          </p>
          <ul>
            <li>Personalizar sua experincia</li>
            <li>Melhorar nosso app</li>
            <li>Enviar emails peridicos</li>
            <li>Fornecer suporte adequado</li>
          </ul>

          <h2>Proteo de Informaes</h2>
          <p>
            Implementamos uma variedade de medidas de segurana para manter a segurana de suas
            informaes pessoais. Utilizamos criptografia de ponta para proteger informaes sensveis
            transmitidas online.
          </p>

          <h2>Compartilhamento de Informaes</h2>
          <p>
            No vendemos, comercializamos ou transferimos para terceiros suas informaes pessoais
            identificveis. Isso no inclui terceiros confiveis que nos ajudam a operar nosso app,
            desde que concordem em manter essas informaes confidenciais.
          </p>

          <h2>Consentimento</h2>
          <p>
            Ao usar nosso app, voc concorda com nossa poltica de privacidade.
          </p>
        </Content>
      </Container>
    </Section>
  );
}; 